import React, { useEffect, useState } from 'react'
import { useForm } from "react-hook-form"
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate, useParams } from 'react-router-dom'
import { categorydata, getcategory, updatecategory } from './categorySlice'

export default function Categoryedit() {
  const {register,handleSubmit,reset} = useForm()
    const [selectedImage, setSelectedImage] = useState(null);
    const [selectname, setselectname] = useState()
    const dispatch = useDispatch()
    const {category} = useSelector(categorydata)
    const navigate = useNavigate()
    const {id} = useParams()

    const handleinput = (e)=>{
      setselectname(e.target.value)
    }

    const onSubmit =  (data) =>{
        	
        const formData = new FormData()
        let name = selectname ? selectname : category.name
        let image = selectedImage ? selectedImage : category.image;
        formData.append('name',name)
        formData.append('image',image)  
        formData.append('oldimage',category.image)
        
         dispatch(updatecategory({id,formData}));
         navigate('/dashboard/categories')
         reset();
    }

    useEffect(()=>{
        dispatch(getcategory(id))
    },[dispatch,id])



  return (
    <div className='max-w-7xl bg-white py-3  lg:mx-auto lg:p-3'>

 <form noValidate onSubmit={handleSubmit(onSubmit)}>
      <div className="space-y-12">
        

        <div className="border-b border-gray-900/10 pb-12">
          <h1 className="text-base text-center font-bold text-2xl font-semibold leading-7 text-gray-900">Edit Categories</h1>
          
          <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
            

            <div className="sm:col-span-6">
              <label htmlFor="name" className="block text-sm font-medium leading-6 text-gray-900">
                Category Name
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  id='name'
                  {...register("name")}
                  defaultValue={category.name || ''}
                  onChange={handleinput}
                  autoComplete="family-name"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-3">
              <label htmlFor="image" className="block text-sm font-medium leading-6 text-gray-900">
                Category Image
              </label>
              <div className="mt-2">
			  <input
			      {...register('image')}
				  type='file'
          id='image'
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  onChange={(e)=>setSelectedImage(e.target.files[0])}
                />
              </div>
			  
            </div>

            <div className="sm:col-span-3">
             
              <div className="mt-2">

                <img src={`/${category.image}`} alt=""/>
			  
              </div>

            </div>
          </div>
          
        </div>
        <div className='w-full'>
        <button
          type="submit"
          className="rounded-md w-full bg-indigo-600 px-3 py-4 mb-3 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
        >
          Edit Category
        </button>
        </div>
      </div>
    </form>

   </div>
  )
}
