import React, { useEffect } from 'react'
import { useForm } from "react-hook-form"
import { useParams } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { allblogdata, createcomment,fetchsingleblog, getcomment } from './blog.slice'


const product = {
    name: 'Basic Tee 6-Pack',
    price: '$192',
    href: '#',
    breadcrumbs: [
      { id: 1, name: 'Men', href: '#' },
      { id: 2, name: 'Clothing', href: '#' },
    ],
    images: [
      {
        src: 'https://tailwindui.com/img/ecommerce-images/product-page-02-secondary-product-shot.jpg',
        alt: 'Two each of gray, white, and black shirts laying flat.',
      },
      {
        src: 'https://tailwindui.com/img/ecommerce-images/product-page-02-tertiary-product-shot-01.jpg',
        alt: 'Model wearing plain black basic tee.',
      },
      {
        src: 'https://tailwindui.com/img/ecommerce-images/product-page-02-tertiary-product-shot-02.jpg',
        alt: 'Model wearing plain gray basic tee.',
      },
      {
        src: 'https://tailwindui.com/img/ecommerce-images/product-page-02-featured-product-shot.jpg',
        alt: 'Model wearing plain white basic tee.',
      },
    ],
    colors: [
      { name: 'White', class: 'bg-white', selectedClass: 'ring-gray-400' },
      { name: 'Gray', class: 'bg-gray-200', selectedClass: 'ring-gray-400' },
      { name: 'Black', class: 'bg-gray-900', selectedClass: 'ring-gray-900' },
    ],
    sizes: [
      { name: 'XXS', inStock: false },
      { name: 'XS', inStock: true },
      { name: 'S', inStock: true },
      { name: 'M', inStock: true },
      { name: 'L', inStock: true },
      { name: 'XL', inStock: true },
      { name: '2XL', inStock: true },
      { name: '3XL', inStock: true },
    ],
    description:
      'The Basic Tee 6-Pack allows you to fully express your vibrant personality with three grayscale options. Feeling adventurous? Put on a heather gray tee. Want to be a trendsetter? Try our exclusive colorway: "Black". Need to add an extra pop of color to your outfit? Our white tee has you covered.',
    highlights: [
      'Hand cut and sewn locally',
      'Dyed with our proprietary colors',
      'Pre-washed & pre-shrunk',
      'Ultra-soft 100% cotton',
    ],
    details:
      'The 6-Pack includes two black, two white, and two heather gray Basic Tees. Sign up for our subscription service and be the first to get new, exciting colors, like our upcoming "Charcoal Gray" limited release.',
  }


  
  
  

export default function Blogdetail() {
    const {register,handleSubmit,reset} = useForm()
    const dispatch = useDispatch()
    let token = localStorage.getItem('token')
    let {blogdata,cmentdata} = useSelector(allblogdata)
    let {id} = useParams()

    
    const onSubmit = (data) =>{
      
      if(!token){
        alert('please login to comment on post')
      }
      else{
        dispatch(createcomment(data))
        reset()
        alert('comment subitted successfully')
        dispatch(getcomment(id))
        console.log(data)
      }
    }

    useEffect(()=>{

      dispatch(fetchsingleblog(id))
      dispatch(getcomment(id))

  },[dispatch,id])

  return (
    <>
    {
      blogdata && 
      <div className='grid grid-cols-1 lg:grid-cols-5'>

        <div className='lg:col-span-3'>

        <div className="bg-cblue">
      <div className="pt-6">
        
        {/* Image gallery */}
        <div className="mx-auto mt-6 max-w-2xl sm:px-6 lg:grid lg:max-w-7xl lg:grid-cols-3 lg:gap-x-8 lg:px-8">
          <div className="aspect-h-4 lg:h-[450px] lg:w-[800px] aspect-w-3 hidden overflow-hidden rounded-lg lg:block">
              <img
              src={`/${blogdata.image}`}
              alt={blogdata.image}
              className="h-full w-full object-cover object-center"
            />
            
            
          </div>
          
          {/* For MObile picture */}
          <div className="aspect-h-5 aspect-w-4 lg:hidden lg:aspect-w-3 sm:overflow-hidden sm:rounded-lg">
            <img
              src={`/${blogdata.image}`}
              alt={product.images[3].alt}
              className="h-full w-full object-cover object-center"
            />
          </div>
        </div>

        {/* Product info */}
        <div className="mx-auto lg:mx-start max-w-2xl px-4 pb-16 pt-10 sm:px-6 lg:grid lg:max-w-7xl lg:grid-cols-3 lg:grid-rows-[auto,auto,1fr] lg:gap-x-8 lg:px-8 lg:pb-24 lg:pt-16  w-full">
          <div className="lg:col-span-2 lg:w-[900px] lg:pr-8">
            <h1 className="text-2xl font-bold tracking-tight text-white sm:text-3xl">{blogdata && blogdata.name}</h1>
            <p className='text-md font-bold  text-white sm:text-md leading-[2.5] pt-4'>{blogdata && blogdata.description}</p>
            
          </div>

          
        </div>
      </div>
       </div>
       <div className='lg:mt[-200px]'>
      <h1 className='text-center text-white font-bold text-2xl'> All Comments</h1>
        <ul  className=" lg:px-[40px] list-none">
      {!cmentdata.length ? (<h1 className='text-white font-bold text-2xl pb-6'>No Coment Yet This Post</h1>) :  cmentdata.map((person,index) => (
        <li key={index} className="flex justify-between gap-x-6 py-5 list-none">
          <div className=" hidden lg:block flex min-w-0 gap-x-4">
            {
              person.user_id && person.user_id.image ? <img className="h-12 w-12 flex-none rounded-full bg-gray-50" src={`/${person.user_id.image}`} alt=""/> : <img className="h-12 w-12 flex-none rounded-full bg-gray-50" src={``} alt=""/>
            }
            <div className="min-w-0 flex-auto">
              <p className="text-sm font-semibold leading-6 text-white">{person.user_id && person.user_id.name}</p>
              <p className="mt-1 truncate text-xs leading-5 text-white">{person.user_id && person.user_id.email}</p>
            </div>
          </div>
          <div className="shrink-0 sm:flex w-full text-center lg:w-[550px] sm:flex-col sm:items-end">
            <h1 className='text-white lg:hidden'>{person.user_id && person.user_id.name}</h1>
            <h1 className='text-white pb-3 lg:hidden'>{person.user_id && person.user_id.email}</h1>
            
            <p className="text-sm leading-6 text-white">{person.comment}</p>
            <div className='lg:hidden'></div>
          </div>
        </li>
      ))}
    </ul>
        </div>

        </div>
        <div className='mt-[-220px] lg:mt-[10px] lg:col-span-2 h-[100vh]'>

        
         <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8 ">
        <div className="sm:mx-auto sm:w-full sm:max-w-sm  lg:-mt-[150px]">
          <h2 className="mt-10 text-center text-white text-2xl font-bold leading-9 tracking-tight text-gray-900">
            Leave A Comment
          </h2>
        </div>

        <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
          <form noValidate className="space-y-6" method="POST" onSubmit={handleSubmit(onSubmit)}>
          <input
                  id="text"
                  {...register("blog_id")}
                  value={id}
                  type="text"
                  className="hidden"
                />
            <div>
              <label htmlFor="cmnt"  className="block text-sm text-white font-medium leading-6 text-gray-900">
                Your Comment
              </label>
              <div className="mt-2">
                <input
                  id="cmnt"
                  {...register("comment", { required: 'comment required'})}
                  type="text"
                  required
                  className="block w-full text-black rounded-md border-0 py-2.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>
            <div>
              <button
                type="submit"
                className="flex w-full justify-center rounded-md bg-indigo-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Submit
              </button>
            </div>
          </form>
        </div>
    </div>
        </div>
    </div>
    }

        
 
    
    </>
  )
}
