import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import axios from 'axios'


export const getallHomeBlog = createAsyncThunk(
    'HomeBlog/getall',
    
    async (_,rejectWithValue) => {
        try{

            const response = await axios.get('/Home/getall')
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
)

export const getHomeBlog = createAsyncThunk(
    'HomeBlog/getdata',
    
    async (id,rejectWithValue) => {
        try{

            const response = await axios.get(`/Home/getdata/${id}`)
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
)

export const getlatestHomeBlog = createAsyncThunk(
    'HomeBlog/get',
    
    async (_,rejectWithValue) => {
        try{

            const response = await axios.get('/Home/get')
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
)

export const createHomeBlog = createAsyncThunk(
    'HomeBlog/create',
    
    async (formData,rejectWithValue) => {
        try{

            const response = await axios.post('/Home/create',formData)
            console.log(response)
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
)

export const updateHomeBlog = createAsyncThunk(
    'HomeBlog/update',
    
    async ({id,formData},rejectWithValue) => {
        try{

            const response = await axios.post(`/Home/update/${id}`,formData)
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
)

export const delHomeBlog = createAsyncThunk(
    'HomeBlog/delete',
    
    async (id,rejectWithValue) => {
        try{

            const response = await axios.delete(`/Home/del/${id}`)
            console.log(response)
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
)

const initialState = {
     blog:[],
     lblog:[],
     loading:true,
     error:false,
     blerrmsg:[]
    
    }

const blogSlice = createSlice({
  name: 'Blogs',
  initialState,
  reducers: {
    increment(state) {
      state.value++
    },
  },
  extraReducers: (builder) => {
    
    builder
    .addCase(createHomeBlog.pending, (state) => {
      
        state.loading = true
        state.error = false
    })
    .addCase(createHomeBlog.fulfilled, (state, action) => {

        state.loading = false
        state.error = false
        state.blog.push(action.payload)
    })
    .addCase(createHomeBlog.rejected, (state, action) => {
    state.loading = false
    state.error = true
    state.blerrmsg = action.payload
    })
    .addCase(getallHomeBlog.pending, (state) => {
      
        state.loading = true
        state.error = false
    })
    .addCase(getallHomeBlog.fulfilled, (state, action) => {

        state.loading = false
        state.error = false
        state.blog = action.payload
    })
    .addCase(getallHomeBlog.rejected, (state, action) => {
    state.loading = false
    state.error = true
    state.blerrmsg = action.payload
    })
    .addCase(updateHomeBlog.pending, (state) => {
      
        state.loading = true
        state.error = false
    })
    .addCase(updateHomeBlog.fulfilled, (state, action) => {

        state.loading = false
        state.error = false
        state.blog = action.payload
    })
    .addCase(getHomeBlog.pending, (state) => {
        state.loading = true
        state.error = false
    })
    .addCase(getHomeBlog.fulfilled, (state, action) => {

        state.loading = false
        state.error = false
        state.blog = action.payload
    })
    .addCase(getlatestHomeBlog.fulfilled, (state, action) => {

        state.loading = false
        state.error = false
        state.lblog = action.payload
    })
    .addCase(delHomeBlog.fulfilled, (state, action) => {

        state.loading = false
        state.error = false
        let index = state.blog.findIndex(el => el._id === action.payload._id)
        state.blog.splice(index,1)
    })
  },
})

export const { increment} = blogSlice.actions
export const Homeblogdatas = state => state.Blogs
export default blogSlice.reducer