import { configureStore } from '@reduxjs/toolkit';
//import counterReducer from '../features/counter/counterSlice';
import categoryReducer from '../features/AdminFeatures/Category/categorySlice';
import blogReducer from '../features/AdminFeatures/Home/adminHomeSlice'
import allblogReducer from '../features/Blog/blog.slice'
import userReducer from '../features/Auth/userSlice';


export const store = configureStore({
  reducer: {
    categories: categoryReducer,
    Blogs:blogReducer,
    blogdatas:allblogReducer,
    users:userReducer
  },
});
