import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { categorydata, getallcategory } from '../AdminFeatures/Category/categorySlice'
import { Homeblogdatas, getallHomeBlog } from '../AdminFeatures/Home/adminHomeSlice'
import { Link } from 'react-router-dom'



export default function MainContent() {
    const dispatch = useDispatch()
    const {category} = useSelector(categorydata)
    const {blog} = useSelector(Homeblogdatas)

    useEffect(()=>{
        dispatch(getallcategory())
        dispatch(getallHomeBlog())

    },[dispatch])


  return (
      <>
        <div className='flex justify-between items-center  flex-wrap my-[40px]  md:my-[70px] lg:p-4  mb-4 md:p-4 p-4'>

    {
        blog && blog.map((blog,index)=>{
            return(

                <div key={index} className=' md:w-[350px] w-full items-center mb-[20px]  md:mb-[40px]' >
        <Link to={`/category/page/${blog.category_id._id}`}>
        <img src={`/${blog.image}`} alt="" className='lg:w-[350px] h-[250px] w-80%'style={{border:'15px solid blue', borderRadius: '40px 20px 40px 20px'}}/>
        </Link>

        <div className='text-white pt-3 lg:pl-2 text-center md:text-start'>
        <Link to={`/category/page/${blog.category_id._id}`} className="bg-blue-800 text-blue-500 font-bold py-2  px-4 rounded-lg">
        {blog.category_id && blog.category_id.name}
        </Link>
        
        <h1 className='pt-2'>{blog.name}</h1>

        <p className='text-gray-400 text-center -ml-2  pt-2 lg:md:leading-8 ' style={{maxHeight: '8em',overflow: 'hidden',textOverflow: 'ellipsis',whiteSpace: 'normal',position: 'relative'}}>{blog.description}</p>
        </div>

    </div>

            )
        })
    }





   
        </div>

    < div className="border-2 border-black-700 my-6"></div>

    
    <div className='my-6'>
        <div className='md:mx-auto md:w-[500px] text-center'>
            {
                category && category.map((cat,index)=>{
                    return(
                        <Link key={index} to={`/category/page/${cat._id}`}>
                        <button   className="bg-blue-800 text-blue-500 font-bold py-2 m-2   px-4 rounded-lg"> 
                        {cat.name}
                        </button>
                        </Link>
                    )
                })

            }
        </div> 
    </div>
</>
    

    
  )
}
