import React, { useEffect, useState } from 'react'
import { useForm } from "react-hook-form"
import { useNavigate, useParams } from 'react-router-dom'
import { allblogdata, fetchSingleblogdata, updateblogdata } from './blog.slice'
import { categorydata, getallcategory } from '../AdminFeatures/Category/categorySlice'
import { useDispatch, useSelector } from 'react-redux'

export default function BlogDashboardEdit() {
    const {register,handleSubmit,reset} = useForm()
    const [selectedImage, setSelectedImage] = useState(null)
    const dispatch = useDispatch()
    const {id} = useParams()
    let token = localStorage.getItem('token')
    const {blogdata,loading} = useSelector(allblogdata)
    const {category} = useSelector(categorydata)
    const navigate = useNavigate()

    const onSubmit =  (data) =>{
        const formData = new FormData()
        let image = selectedImage ? selectedImage : blogdata.image
        formData.append('name',data.name)
        formData.append('category_id',data.category)
        formData.append('description',data.description)
        formData.append('image',image)
        formData.append('oldimage',blogdata.image)
        dispatch(updateblogdata({id,formData}))
        reset()
        navigate('/blog/dashboard/page')
    }

    useEffect(()=>{
        dispatch(fetchSingleblogdata(id))
        dispatch(getallcategory())
    },[dispatch,id])
    

  return (
    <>

    {
        !token ? (alert('please login to access dashboard')) : 
        loading ? (<h1>Loadinggg</h1>) : (

            <div className='max-w-7xl bg-white py-3  lg:mx-auto lg:p-3'>

            <form noValidate onSubmit={handleSubmit(onSubmit)}>
            <div className="space-y-12">
                

                <div className="border-b border-gray-900/10 pb-12">
                <h1 className="text-base text-center font-bold text-2xl font-semibold leading-7 text-gray-900">Edit Blog Post</h1>
                
                <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                    
                <div className="sm:col-span-6">
                    <label htmlFor="name" className="block text-sm font-medium leading-6 text-gray-900">
                        Blog Title
                    </label>
                    <div className="mt-2">
                        {
                            blogdata.name &&
                            <input
                        type="text"
                        id='name'
                        {...register("name")}
                        defaultValue={blogdata.name}
                        autoComplete="family-name"
                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                        />
                        }
                        
                    </div>
                    </div>

                    <div className="sm:col-span-2">
                    <label htmlFor="category" className="block text-sm font-medium leading-6 text-gray-900">
                        Category
                    </label>
                    {
                        blogdata.category_id &&

                        <div className="mt-2">
                        <select
                        id="category"
                        {...register("category", { required: 'category required'})}
                        autoComplete="country-name"
                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:max-w-xs sm:text-sm sm:leading-6"
                        >
                            <option  value={blogdata.category_id._id}>{blogdata.category_id.name}</option>

                        {
                            category && category.map((cat,index)=>{
                                return(
                                    <option key={index} value={cat._id}>{cat.name}</option>
                                )

                            })
                        }
                        </select>
                    </div>
                    }
                   
                    </div>

                    <div className="sm:col-span-2">
                    <label htmlFor="image" className="block text-sm font-medium leading-6 text-gray-900">
                        Category Image
                    </label>
                    <div className="mt-2">
                    <input
                        {...register('image')}
                        id='image'
                        type='file'
                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          onChange={(e)=>setSelectedImage(e.target.files[0])}
                        />
                    </div>
                    
                    </div>

                    <div className="sm:col-span-2">
                    
                    <div className="mt-2">

                        <img className='w-[150px] h-[120px]' src={`/${blogdata.image}`} alt=""/>
                    
                    </div>

                    </div>

                    <div className="sm:col-span-6">
                    <label htmlFor="des" className="block text-sm font-medium leading-6 text-gray-900">
                        Blog Description
                    </label>
                    <div className="mt-2">
                        {
                            blogdata.description &&

                            <input
                        type="text"
                        id='des'
                        {...register("description")}
                        defaultValue={blogdata.description}
                        //   onChange={handleinput}
                        autoComplete="family-name"
                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                        />
                        }
                        
                    </div>
                    </div>

                </div>
                
                </div>
                <div className='w-full'>
                <button
                type="submit"
                className="rounded-md w-full bg-indigo-600 px-3 py-4 mb-3 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                >
                Edit Home Blog
                </button>
                </div>
            </div>
            </form>

            </div>

        )
    }
    
   </>
  )
}
