import React, { useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'
import { allblogdata, fetchgetcatblog } from './Blog/blog.slice'
import { useDispatch, useSelector } from 'react-redux'

export default function BlogCat() {
    const dispatch = useDispatch()
    const {blogdata} = useSelector(allblogdata)
    let {id} = useParams() 

    useEffect(()=>{

        dispatch(fetchgetcatblog(id))

    },[dispatch,id])
  return (
    <>
    {
        blogdata.length &&
   
        <div className='flex justify-between items-center  flex-wrap my-[40px]  md:my-[70px] lg:p-4  mb-4 md:p-4 p-4'>

    {
        blogdata && blogdata.map((blog,index)=>{
            return(

                <div key={index} className=' md:w-[350px] h:[550px] md:h-[550px] lg:h-[550px] w-full items-center mb-[20px]  md:mb-[40px]'>
        <Link to={`/blog/detail/page/${blog._id}`}>
        <img src={`/${blog.image}`} alt="" className='lg:w-[350px] h-[250px] md:h-[250px] lg:w-[250px] w-80%'style={{border:'15px solid blue', borderRadius: '40px 20px 40px 20px'}}/>
        </Link>

        <div className='text-white pt-4 lg:pl-2 text-center md:text-start'>
        <Link to={`/blog/detail/page/${blog._id}`} className="bg-blue-800 text-blue-500 font-bold py-2  px-4 rounded-lg">
        {blog.category_id && blog.category_id.name}
        </Link>
        <h1 className='pt-2 md:pl-2 lg:pl-2'>{blog.name}</h1>

        <p className='text-gray-400 text-center md:text-start md:pl-4 lg:pl-4 -ml-2  pt-2 lg:md:leading-8 ' style={{maxHeight: '8em',overflow: 'hidden',textOverflow: 'ellipsis',whiteSpace: 'normal',position: 'relative'}}>{blog.description}</p>
        </div>
    </div>

            )
        })
    }   
        </div>
     }    
    </>
  )
}
