import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import axios from 'axios';

export const createcategory = createAsyncThunk(
    'category/create',
    async (item,rejectWithValue) => {
        try{
            let response = await axios.post('/category/create',item)
            return response.data.data

        }
        catch(error){
            console.log(error)
            return rejectWithValue(error)
        }
    },
  )

export const getallcategory = createAsyncThunk(
    'category/getall',
    async (_,rejectWithValue) => {
        try{
            let response = await axios.get('/category/get')
            
            return response.data.data

        }
        catch(error){
            console.log(error)
            return rejectWithValue(error)
        }
    },
  )

export const getcategory = createAsyncThunk(
    'category/get',
    async (id,rejectWithValue) => {
        try{
            let response = await axios.get(`/category/get/${id}`)
            
            return response.data.data

        }
        catch(error){
            console.log(error)
            return rejectWithValue(error)
        }
    },
  )

export const updatecategory = createAsyncThunk(
    'category/update',
    async ({id,formData},rejectWithValue) => {

        try{
            let response = await axios.patch(`/category/update/data/${id}`,formData);
            
            return response.data.data

        }
        catch(error){
            return rejectWithValue(error)
        }
    },
  )

export const delcategory = createAsyncThunk(
    'category/del',
    async (id,rejectWithValue) => {

        try{

            let response = await axios.delete(`/category/del/${id}`);
            return response.data.data

        }
        catch(error){
            return rejectWithValue(error)
        }
    },
  )


const initialState = {
     category: [],
     loading:true,
     error:false,
     caterr:[],
    }

const categorySlice = createSlice({
  name: 'categories',
  initialState,
  reducers: {
    increment(state) {
      state.value++
    },
  },

  extraReducers: (builder) => {
    builder
    .addCase(createcategory.pending, (state, action) => {
        state.loading = true
        state.error = false
    })
    .addCase(createcategory.fulfilled, (state, action) => {
     state.loading = false
     state.error = false
     state.category.push(action.payload)
    })
    .addCase(createcategory.rejected, (state, action) => {
     state.loading = false
     state.error = true
     state.caterr = action.payload
    })
    .addCase(getallcategory.pending, (state, action) => {
        state.loading = true
        state.error = false
    })
    .addCase(getallcategory.fulfilled, (state, action) => {
     state.loading = false
     state.error = false
     state.category = action.payload
    })
    .addCase(getallcategory.rejected, (state, action) => {
     state.loading = false
     state.error = true
     state.caterr = action.payload
    })
    .addCase(getcategory.pending, (state, action) => {
        state.loading = true
        state.error = false
    })
    .addCase(getcategory.fulfilled, (state, action) => {
     state.loading = false
     state.error = false
     state.category = action.payload
    })
    .addCase(getcategory.rejected, (state, action) => {
     state.loading = false
     state.error = true
     state.caterr = action.payload
    })
    .addCase(updatecategory.pending, (state, action) => {
        state.loading = true
        state.error = false
    })
    .addCase(updatecategory.fulfilled, (state, action) => {
     state.loading = false
     state.error = false
     state.category = action.payload
    })
    .addCase(updatecategory.rejected, (state, action) => {
     state.loading = false
     state.error = true
     state.caterr = action.payload
    })
    .addCase(delcategory.fulfilled, (state, action) => {
        state.loading = false
        state.error = false
        let index = state.category.findIndex(el => el._id === action.payload._id)
        state.category.splice(index,1)
       })
  },
})

export const { increment} = categorySlice.actions
export const categorydata = state => state.categories
export default categorySlice.reducer