import React, { useState } from 'react'
import { useForm } from "react-hook-form"
import { useDispatch, useSelector } from 'react-redux'
import { UserUpdate, userdata } from './userSlice'
import { useNavigate, useParams } from 'react-router-dom'

export default function UpdateProfile() {
    const {register,handleSubmit,reset,formState: { errors },} = useForm()
    const [selectedImage, setSelectedImage] = useState(null);
    let {user, loading} = useSelector(userdata)
    let navigate = useNavigate()
    let {id} = useParams()
    let dispatch = useDispatch()

    const onSubmit =  (data) =>{
      
        const formData = new FormData()

        formData.append('name',data.name)
        let image = selectedImage ? selectedImage : data.image
        formData.append('image',image)
        formData.append('oldimage',data.image)

        dispatch(UserUpdate({id,formData}))
        	
          navigate('/profile/page')
          reset();
    }
  return (
    <>
    {
        loading ? (<h1>Loading...</h1>) : 

        <div className='max-w-7xl bg-white py-3  lg:mx-auto lg:p-3'>

        <form noValidate onSubmit={handleSubmit(onSubmit)}>
             <div className="space-y-12">
               
       
               <div className="border-b border-gray-900/10 pb-12">
                 <h1 className="text-base text-center font-bold text-2xl font-semibold leading-7 text-gray-900">Edit Profile</h1>
                 
                 <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                   
       
                   <div className="sm:col-span-3">
                     <label htmlFor="last-name" className="block text-sm font-medium leading-6 text-gray-900">
                       Your Name
                     </label>
                     <div className="mt-2">
                       <input
                         type="text"
                         id='text'
                         {...register("name")}
                         defaultValue={user.name}
                         autoComplete="family-name"
                         className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                       />
                     </div>
                     {errors.name && <p className='text-red-500'>{errors.name.message}</p>}
                   </div>
       
                   <div className="sm:col-span-3">
                     <label htmlFor="image" className="block text-sm font-medium leading-6 text-gray-900">
                       Your Image
                     </label>
                     <div className="mt-2">
                     <input
                         id='image'
                         {...register('image')}
                         type='file'
                         className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                         onChange={(e)=>setSelectedImage(e.target.files[0])}
                       />
                     </div>
                     
                   </div>
       
                   <div className="sm:col-span-3">
                    
                     <div className="mt-2">
       
                       <img src={`/${user.image}`} alt=""/>
                     
                     </div>
       
                   </div>
                 </div>
                 
               </div>
               <div className='w-full'>
               <button
                 type="submit"
                 className="rounded-md w-full bg-indigo-600 px-3 py-4 mb-3 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
               >
                 Update Profile
               </button>
               </div>
             </div>
           </form>
       
          </div>
    }
    
   
   </>
  )
}
