import React, { useEffect, useState } from 'react'
import { useForm } from "react-hook-form"
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { categorydata, getallcategory } from '../Category/categorySlice'
import { Homeblogdatas, createHomeBlog, delHomeBlog, getallHomeBlog } from './adminHomeSlice'


export default function AdminHome() {
    const {register,handleSubmit,reset,formState: { errors }} = useForm()
    const dispatch = useDispatch()
    const {category} = useSelector(categorydata)
    const [submit, setsubmit] = useState(false)
    const {blog,loading} = useSelector(Homeblogdatas)
  

    const onSubmit = (data) =>{

        console.log(data)
        const formData = new FormData()

        formData.append('name',data.name)
        formData.append('category',data.category)
        formData.append('image',data.image[0])
        formData.append('description',data.description)

        dispatch(createHomeBlog(formData))
        setsubmit(true)
        reset()
    }

    const handledel = (id)=>{

      dispatch(delHomeBlog(id))

    }

    useEffect(()=>{

        if(submit){
          dispatch(getallHomeBlog())
          setsubmit(false)
        }

    },[dispatch,submit])

    useEffect(()=>{

      dispatch(getallcategory())
      dispatch(getallHomeBlog())

    },[dispatch])



  return (
    <div className="container-fluid">
	<h1 className="mb-8 mt-8 text-center text-white font-bold text-3xl">
    Home Page Categories
  </h1>


{

  loading ? (<h1>Loadingggg</h1>) : (

    <table className="text-left w-full bg-white">
		<thead className="bg-black flex text-white w-full">
			<tr className="flex w-full mb-4">
				<th className="p-4 w-1/6">S.No</th>
				<th className="p-4 w-1/6">Title</th>
        <th className="p-4 w-1/6">Category</th>
				<th className="p-4 w-1/6">Image</th>
				<th className="p-4 w-1/6">Actions</th>
        <th className="p-4 w-1/6">Description</th>
			</tr>
		</thead>
    {/* <!-- Remove the nasty inline CSS fixed height on production and replace it with a CSS className — this is just for demonstration purposes! --> */}

		<tbody className="bg-grey-light flex flex-col items-center  overflow-y-scroll w-full" style={{height: "50vh"}}>

      {
        blog && blog.length > 0 && blog.map((blog,index)=>{
          return(

            <tr key={index} className="flex w-full mb-4">
				<td className="p-4 w-1/6 mt-4">{index}</td>
				<td className="p-4 w-1/6 mt-4">{blog.name}</td>
        <td className="p-4  w-1/6 mt-4">{blog.category_id && blog.category_id.name}</td>
				<td className="p-4 w-1/6"><img src={`/${blog.image}`} alt="" style={{height:'100px', border:"5px solid black"}}/></td>
				<td className="p-4 w-1/6">
				<Link to={`/admin/home/edit/page/${blog._id}`} className="bg-blue-900 my-1 mt-7 text-white px-5 py-2 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50">Update</Link>
				<button onClick={(e)=>handledel(blog._id)}  className="bg-blue-900 my-1 text-white mt-3 px-6 py-2 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50">Delete</button>
				</td>
        <td className="p-2  w-1/6 mt-4">{blog.description}</td>
			  </tr>

          )
        })
      }
		</tbody>
	    </table>

  )

}
			
			
 <div className='max-w-7xl bg-white mt-4 lg:mx-auto lg:p-3'>

 <form noValidate onSubmit={handleSubmit(onSubmit)} method='post'>
      <div className="space-y-12">
        

        <div className="border-b border-gray-900/10 pb-12">
          <h2 className="text-base text-center font-bold text-2xl font-semibold leading-7 text-gray-900">Add Home Blogs</h2>
          
          <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">

          <div className="sm:col-span-2">
              <label htmlFor="last-name" className="block text-sm font-medium leading-6 text-gray-900">
                Blog Name
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  {...register("name", { required: 'Name required'})}
                  id="last-name"
                  autoComplete="family-name"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>


          <div className="sm:col-span-2">
              <label htmlFor="category" className="block text-sm font-medium leading-6 text-gray-900">
                Category
              </label>
              <div className="mt-2">
                <select
                  id="category"
                  {...register("category", { required: 'category required'})}
                  autoComplete="country-name"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:max-w-xs sm:text-sm sm:leading-6"
                >

                    {
                        category && category.map((cat,index)=>{
                            return(

                                <option key={index} value={cat._id}>{cat.name}</option>

                            )
                        })
                    }
                </select>
              </div>
            </div>

            <div className="sm:col-span-2">
              <label htmlFor="image" className="block text-sm font-medium leading-6 text-gray-900">
                Category Image
              </label>
              <div className="mt-2">
			  <input
			      {...register("image", { required: 'image required'})}
				  type='file'
          id='image'
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6" 
                />
              </div>
			  {errors.image && <p className='text-red-500'>{errors.image.message}</p>}
            </div>

            <div className="sm:col-span-6">
              <label htmlFor="des" className="block text-sm font-medium leading-6 text-gray-900">
                Blog Description
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  {...register("description", { required: 'description required'})}
                  id="des"
                  autoComplete="family-name"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>
          </div>
          
        </div>
        <div className='w-full'>
        <button
          type="submit"
          className="rounded-md w-full bg-indigo-600 px-3 py-4 mb-3 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
        >
          Add Home Blog
        </button>
        </div>
      </div>
    </form>

 </div>
   
    </div>
  )
}
