import React from 'react'
import {Routes, Route} from "react-router-dom";
import HomePage from './Pages/HomePage'
import CategoryPage from './AdminPages/CategoryPage';
import CategoryEditPage from './AdminPages/CategoryEditPage';
import AdminHomePage from './AdminPages/AdminHomePage';
import AdminHomeEditPage from './AdminPages/AdminHomeEditPage';
import BlogPage from './Pages/BlogPage';
import BlogDashboardPage from './Pages/BlogDashboardPage';
import LoginPage from './Pages/LoginPage';
import RegisterPage from './Pages/RegisterPage';
import Logou from './features/Auth/Logou';
import BlogDashboardEditPage from './Pages/BlogDashboardEditPage';
import Protect from './features/Protect';
import BlogCatPage from './Pages/BlogCatPage';
import BlogdetailPage from './Pages/BlogdetailPage';
import AdminBlogPage from './AdminPages/AdminBlogPage';
import AdminProtect from './features/AdminProtect';
import ProfilePage from './Pages/ProfilePage';
import UpdateProfilePage from './Pages/UpdateProfilePage';
import UpdatePasswordPage from './Pages/UpdatePasswordPage';
import ForgotPasswordPage from './Pages/ForgotPasswordPage';
import ResetPasswordPage from './Pages/ResetPasswordPage';

export default function App() {

  return (
    <div className='bg-cblue'>
      <Routes>

        {/* All Admins Routes Start Here */}

      <Route path={'/dashboard/categories'} element={<AdminProtect children={<CategoryPage/>}></AdminProtect>}/>
      <Route path={'/dashboard/editcategories/:id'} element={<AdminProtect children={<CategoryEditPage/>}></AdminProtect>}/>
      <Route path={'/admin/home/page'} element={<AdminProtect children={<AdminHomePage/>}></AdminProtect> }/>
      <Route path={'/admin/blog/page'} element={<AdminProtect children={<AdminBlogPage/>}></AdminProtect>}/>
      <Route path={'/admin/home/edit/page/:id'} element={<AdminProtect children={<AdminHomeEditPage/>}></AdminProtect> }/>

      {/* All Admins Route Ends Here */}

 
       {/* All User Access Routes Start */}

      <Route path={'/'} element={<HomePage/>}/>
      <Route path={'/blog/page'} element={<BlogPage/>}/>
      <Route path={'/category/page/:id'} element={<BlogCatPage/>}/>
      <Route path={'/blog/detail/page/:id'} element={<BlogdetailPage/>}/>
      <Route path={'/login/page'} element={<LoginPage/>}/>
      <Route path={'/register/page'} element={<RegisterPage/>}/>
      <Route path={'/forgot/password/page'} element={<ForgotPasswordPage/>}/>
      <Route path={'/reset/password/page/:tokenn'} element={<ResetPasswordPage/>}/>
      <Route path={'/logout/page'} element={<Logou/>}/>

      {/* All User Access Routes End Here */}

      
      {/* All Authenticate User Routes Starts Here */}

      <Route path={'/blog/dashboard/page'} element={<Protect children={<BlogDashboardPage/>}></Protect>}/>
      <Route path={'/blog/dashboard/edit/page/:id'} element={<Protect children={<BlogDashboardEditPage/>}></Protect>}/>
      <Route path={'/profile/page'} element={<Protect children={<ProfilePage/>}></Protect>}/>
      <Route path={'/update/password/page'} element={<Protect children={<UpdatePasswordPage/>}></Protect>}/>
      <Route path={'update/profile/page/:id'} element={<Protect children={<UpdateProfilePage/>}></Protect>}/>

      {/* All Authenticate User Routes End Here */}

      </Routes>
    </div>
  )
}
