import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'
import {useDispatch, useSelector} from 'react-redux'
import { useForm } from "react-hook-form"
import { categorydata, createcategory, delcategory, getallcategory} from './categorySlice'

export default function Category() {
    const {register,handleSubmit,reset,formState: { errors }} = useForm()
    const dispath = useDispatch()
    const {category, loading} = useSelector(categorydata)


const onSubmit = data =>{
	
	const formData = new FormData()
	formData.append('name',data.name)
	formData.append('image',data.image[0])    

	dispath(createcategory(formData))
	dispath(getallcategory())
	reset();
	
}

const handledelete = (id)=>{
  dispath(delcategory(id))
  dispath(getallcategory())
}

  useEffect(()=>{

	dispath(getallcategory())

  },[dispath])

  return (
    <div className="container-fluid">
	<h1 className="mb-8 mt-8 text-center text-white font-bold text-3xl">
    All Categories
  </h1>

  {
		loading ? (<h1 className='text-white'>Loadinggg....</h1>) : (

			<table className="text-left w-full bg-white">
		<thead className="bg-black flex text-white w-full">
			<tr className="flex w-full mb-4">
				<th className="p-4 w-1/4">S.No</th>
				<th className="p-4 w-1/4">Title</th>
				<th className="p-4 w-1/4">Image</th>
				<th className="p-4 w-1/4">Actions</th>
			</tr>
		</thead>
    {/* <!-- Remove the nasty inline CSS fixed height on production and replace it with a CSS className — this is just for demonstration purposes! --> */}

	
		<tbody className="bg-grey-light flex flex-col items-center  overflow-y-scroll w-full" style={{height: "50vh"}}>
			{
				category && category.length > 0 && category.map((cat,index)=>{

					return(
						<tr key={index} className="flex w-full mb-4">
				<td className="p-4 w-1/4 mt-4">{index}</td>
				<td className="p-4 w-1/4 mt-4">{cat.name}</td>
				<td className="p-4 w-1/4"><img src={`/${cat.image}`} alt="" style={{height:'100px', border:"5px solid black"}}/></td>
				
				<td className="p-4 w-1/4">
				<Link to={`/dashboard/editcategories/${cat._id}`} className="bg-blue-900 my-1 mt-7 text-white px-5 py-2 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50">Update</Link>
				<button onClick={(e)=> handledelete(cat._id)} className="bg-blue-900 my-1 text-white mt-3 px-6 py-2 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50">Delete</button>
				</td>
			          </tr>
					)
				})
			}
			
		</tbody>
	</table>
			
		)
	}

	

 <div className='max-w-7xl bg-white mt-4 lg:mx-auto lg:p-3'>

 <form noValidate onSubmit={handleSubmit(onSubmit)}>
      <div className="space-y-12">
        

        <div className="border-b border-gray-900/10 pb-12">
          <h2 className="text-base text-center font-bold text-2xl font-semibold leading-7 text-gray-900">Add Categories</h2>
          
          <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
            

            <div className="sm:col-span-3">
              <label htmlFor="last-name" className="block text-sm font-medium leading-6 text-gray-900">
                Category Name
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  {...register("name", { required: 'title required'})}
                  id="last-name"
                  autoComplete="family-name"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
              {errors.name && <p className='text-red-500'>{errors.name.message}</p>}
            </div>

            <div className="sm:col-span-3">
              <label htmlFor="image" className="block text-sm font-medium leading-6 text-gray-900">
                Category Image
              </label>
              <div className="mt-2">
			  <input
			      {...register('image')}
				  type='file'
          id='image'
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6" 
                />
              </div>
			  {errors.image && <p className='text-red-500'>{errors.image.message}</p>}
            </div>
          </div>
          
        </div>
        <div className='w-full'>
        <button
          type="submit"
          className="rounded-md w-full bg-indigo-600 px-3 py-4 mb-3 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
        >
          Add Category
        </button>
        </div>
      </div>
    </form>

 </div>
   
</div>
  )
}
