import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import axios from 'axios'
let token = localStorage.getItem('token')

const headers = {
  'Authorization': `Bearer ${token}`,
  // 'Content-Type': 'application/json', // Example content type
  // // Add other headers if needed
};


export const fetchblogdata = createAsyncThunk(
    'blog/getall',
    async (_,rejectWithValue) => {

        try{

            const response = await axios.get(`/blog/getall`,{headers})
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
  )

  export const fetchallblogdata = createAsyncThunk(
    'blog/getallblog',
    async (_,rejectWithValue) => {

        try{

            const response = await axios.get(`/blog/allblog`,{headers})
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
  )

  export const fetchgetapprovedata = createAsyncThunk(
    'blog/getapprovedata',
    async (_,rejectWithValue) => {

        try{

            const response = await axios.get(`/blog/getapprovedata`)
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
  ) 

  export const fetchgetcatblog = createAsyncThunk(
    'blog/getcatblog',
    async (id,rejectWithValue) => {

        try{

            const response = await axios.get(`/blog/getcatblog/${id}`)
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
  )

  export const fetchsingleblog = createAsyncThunk(
    'blog/blogsingle',
    async (id,rejectWithValue) => {

        try{

            const response = await axios.get(`/blog/getsingleblog/${id}`)
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
  )
  
  export const fetchSingleblogdata = createAsyncThunk(
    'blog/getdata',
    async (id,rejectWithValue) => {

        try{

            const response = await axios.get(`/blog/getdata/${id}`,{headers})
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
  ) 

export const createblogdata = createAsyncThunk(
    'blog/create',
    async (formData,{rejectWithValue}) => {
        try{

            const response = await axios.post('/blog/create',formData,{headers})
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
  )

  export const updateblogdata = createAsyncThunk(
    'blog/update',
    async ({id,formData},{rejectWithValue}) => {
        try{

            const response = await axios.patch(`/blog/update/${id}`,formData,{headers})
            console.log(response)
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
  )

  export const delblogdata = createAsyncThunk(
    'blog/delete',
    async (id,{rejectWithValue}) => {
        try{

            const response = await axios.delete(`/blog/del/${id}`,{headers})
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
  )

  export const createcomment = createAsyncThunk(
    'comment/create',
    async (data,rejectWithValue) => {

        try{
             console.log(headers)
            const response = await axios.post(`/comment/create`,data,{headers})
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
  )

  export const getcomment = createAsyncThunk(
    'comment/get',
    async (id,rejectWithValue) => {

        try{
            const response = await axios.get(`/comment/get/${id}`)
            return response.data.data

        }catch(error){
            return rejectWithValue(error)

        }
    },
  )

const initialState = { 
    blogdata: [],
    cmentdata:[],
    loading:true,
    error:false,
    errmsg:null, 
}

const blogSlice = createSlice({
  name: 'blogsdatas',
  initialState,
  reducers: {
    increment(state) {
      state.value++
    },
  },
  extraReducers: (builder) => {
    builder
    .addCase(fetchblogdata.pending, (state) => {
      state.loading = true
      state.error = false
    })
    .addCase(fetchblogdata.fulfilled, (state, action) => {
        state.loading = false
        state.error = false
        state.blogdata = action.payload
    })
    .addCase(fetchblogdata.rejected, (state, action) => {
        
        state.loading = false
        state.error = true
        state.errmsg = action.payload
    })
    .addCase(fetchallblogdata.fulfilled, (state, action) => {
      state.loading = false
      state.error = false
      state.blogdata = action.payload
  })
    .addCase(fetchgetapprovedata.pending, (state) => {
      state.loading = true
      state.error = false
    })
    .addCase(fetchgetapprovedata.fulfilled, (state, action) => {
        state.loading = false
        state.error = false
        state.blogdata = action.payload

    })
    .addCase(fetchgetapprovedata.rejected, (state, action) => {
        
        state.loading = false
        state.error = true
        state.errmsg = action.payload
    })
    .addCase(fetchgetcatblog.pending, (state) => {
      state.loading = true
      state.error = false
    })
    .addCase(fetchgetcatblog.fulfilled, (state, action) => {
        state.loading = false
        state.error = false
        state.blogdata = action.payload

    })
    .addCase(fetchgetcatblog.rejected, (state, action) => {
        
        state.loading = false
        state.error = true
        state.errmsg = action.payload
    })
    .addCase(fetchSingleblogdata.pending, (state) => {
      state.loading = true
      state.error = false
    })
    .addCase(fetchSingleblogdata.fulfilled, (state, action) => {
        state.loading = false
        state.error = false
        state.blogdata = action.payload

    })
    .addCase(fetchSingleblogdata.rejected, (state, action) => {
        
        state.loading = false
        state.error = true
        state.errmsg = action.payload
    })
    .addCase(fetchsingleblog.fulfilled, (state, action) => {
      state.loading = false
      state.error = false
      state.blogdata = action.payload

  })
    .addCase(createblogdata.pending, (state) => {
        state.loading = true
        state.error = false
      })
    .addCase(createblogdata.fulfilled, (state, action) => {
        state.loading = false
        state.error = false
        state.blogdata.push(action.payload)

    })
    .addCase(createblogdata.rejected, (state, action) => {
        
        state.loading = false
        state.error = true
        state.errmsg = action.payload
    })
    .addCase(updateblogdata.fulfilled, (state, action) => {
      state.loading = false
      state.error = false
      console.log(action.payload)
      state.blogdata = action.payload
  })

  .addCase(delblogdata.fulfilled, (state, action) => {
    state.loading = false
    state.error = false
    let index = state.blogdata.findIndex(el => el._id === action.payload._id)
    console.log(index)
    state.blogdata.splice(index,1)
})

.addCase(createcomment.fulfilled, (state, action) => {
    state.loading = false
    state.error = false
    state.cmentdata.push(action.payload)
})    
.addCase(getcomment.fulfilled, (state, action) => {
  state.loading = false
  state.error = false
  state.cmentdata = action.payload
}) 
  },
})

export const { increment} = blogSlice.actions
export const allblogdata = state => state.blogdatas
export default blogSlice.reducer