import React, { useEffect} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { fetchUser, userdata } from './userSlice'
import { Link } from 'react-router-dom'




export default function Profile() {
    let {user} = useSelector(userdata)
    console.log(user)
    let token = localStorage.getItem('token')
    let dispatch = useDispatch()

    useEffect(()=>{
      if(token){
        dispatch(fetchUser())

      }

    },[dispatch,token])



  return (
    <>
    {
        user &&   

        <>
        {/* component */}
        <div className="flex items-center mt-[-100px] h-screen w-full justify-center">
          <div className="max-w-xs">
            <div className="bg-white shadow-xl rounded-lg py-3">
              <div className="photo-wrapper p-2">
                <img
                  className="w-32 h-32 rounded-full mx-auto"
                  src={`/${user.image}`}
                  alt='not_avail'
                />
              </div>
              <div className="p-2">
                <h3 className="text-center text-xl text-gray-900 font-medium leading-8">
                  {user.name}
                </h3>
                <div className="text-center text-gray-400 text-xs font-semibold">
                  <p>{user.role}</p>
                </div>
                <table className="text-xs my-3">
                  <tbody>
                    <tr>
                      <td className="px-2 py-2 text-gray-500 font-semibold">
                        Email
                      </td>
                      <td className="px-2 py-2">{user.email}</td>
                    </tr>
                    <tr>
                      <td className="px-2 py-2 text-gray-500 font-semibold">Phone</td>
                      <td className="px-2 py-2">{user.phone}</td>
                    </tr>
                  </tbody>
                </table>
                <div className="text-center my-3">
                  <Link
                    className="text-xs text-indigo-500 italic hover:underline hover:text-indigo-600 font-medium"
                    to={`/update/profile/page/${user._id}`}
                  >
                    Update Profile
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
      
    }

  
    </>
  
  )
}
