import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Homeblogdatas,getlatestHomeBlog } from '../AdminFeatures/Home/adminHomeSlice'
import { Link } from 'react-router-dom'

export default function Banner() {
    const dispatch = useDispatch()
    const {lblog} = useSelector(Homeblogdatas)
    const dta = lblog[0]

    useEffect(()=>{

        dispatch(getlatestHomeBlog())

    },[dispatch])
  return (

    <div className='md:grid grid-cols-5 lg:mx-auto'>

        <div className='col-span-2 lg:ml-[200px] p-2 md:col-span-2'>
            <img className="rounded-3xl" src={dta && dta.image && `/${dta.image}`} alt="" style={{height:'320px', border:'15px solid blue', borderRadius: '40px 20px 40px 20px'}}/>
        </div>

        <div className='col-span-2 md:col-span-3 lg:ml-[50px]  my-1   p-1 text-center lg:w-[700px]'>
        <Link to={`/category/page/${dta && dta.category_id._id}`} className="bg-blue-800 text-blue-500 font-bold py-2 px-4 rounded-lg ">
        {dta && dta.category_id.name && dta.category_id.name}
        </Link>
            <div className='pt-3'>
                <h1 className='text-white font-bold text-lg lg:leading-9 md:leading-9'>{dta && dta.name && dta.name}.</h1>
            </div>
            <div>
                <p className='text-gray-400 pt-2 lg:md:leading-9'>{dta && dta.description && dta.description}</p>
            </div>

        </div>

    </div>
  )
}
