import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { fetchUser, userdata } from './Auth/userSlice'
import { Navigate} from 'react-router-dom'



export default function AdminProtect({children}) {
    const {user} = useSelector(userdata)
    let token = localStorage.getItem('token')
    let dispatch = useDispatch()

    useEffect(()=>{
        if(token){
            dispatch(fetchUser(token))
        }
    },[dispatch,token])

   return(
    <>
    {/* {!user.length && <Navigate to={'/'} replace={true}></Navigate> } */}
    {user && user.role === 'user' && <Navigate to={'/'} replace={true}></Navigate>}
    {user && user.role === 'admin' &&  children}
    </>
   )

    
}
