import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { allblogdata, fetchallblogdata, updateblogdata } from '../../Blog/blog.slice'

export default function AdminBlog() {
    const dispatch = useDispatch()
    const {blogdata,loading} = useSelector(allblogdata)

    const handlepen = (e,blog)=>{

        e.preventDefault()


        let data = {...blog,status:'Pending'}
        let formData = new FormData()

        formData.append('status',data.status)
        let id = blog._id

        
        dispatch(updateblogdata({id,formData}))
        alert('Blog has pending Now')
        dispatch(fetchallblogdata())
    }

    const handleapprove = (e,blog)=>{

        e.preventDefault()


        let data = {...blog,status:'Approved'}
        let formData = new FormData()

        formData.append('status',data.status)
        let id = blog._id
        
        dispatch(updateblogdata({id,formData}))
        alert('Blog has Approved Now')
        dispatch(fetchallblogdata())
    }

    useEffect(()=>{
        dispatch(fetchallblogdata())

    },[dispatch])



  return (
    <div className="container-fluid">
	<h1 className="mb-8 mt-8 text-center text-white font-bold text-3xl">
    Blog Page 
  </h1>
{
  loading ? (<h1>Loadingggg</h1>) : (

    <table className="text-left w-full bg-white">
		<thead className="bg-black flex text-white w-full">
			<tr className="flex w-full mb-4">
				<th className="p-4 w-1/6">S.No</th>
                <th className="p-4 w-1/6">Status</th>
                <th className="p-4 w-1/6">User</th>
				<th className="p-4 w-1/6">Title</th>
        <th className="p-4 w-1/6">Category</th>
				<th className="p-4 w-1/6">Image</th>
				<th className="p-4 w-1/6">Actions</th>
        <th className="p-4 w-1/6">Description</th>
			</tr>
		</thead>
    {/* <!-- Remove the nasty inline CSS fixed height on production and replace it with a CSS className — this is just for demonstration purposes! --> */}
    

		<tbody className="bg-grey-light flex flex-col items-center  overflow-y-scroll w-full" style={{height: "100vh"}}>

      {
        blogdata && blogdata.length > 0 && blogdata.map((blog,index)=>{
          return(

            <tr key={index} className="flex w-full mb-4">
				<td className="p-4 w-1/6 mt-4">{index}</td>
                <td className="p-4 w-1/6 mt-4 p-3 text-blue-600 font-bold text-2xl" >{blog.status}</td>
                <td className="p-4 w-1/6 mt-4">{blog.user && blog.user.name}</td>
				<td className="p-4 w-1/6 mt-4">{blog.name}</td>
        <td className="p-4  w-1/6 mt-4">{blog.category_id && blog.category_id.name}</td>
				<td className="p-4 w-1/6"><img src={`/${blog.image}`} alt="" style={{height:'100px', border:"5px solid black"}}/></td>
                {
                    blog.status === 'Pending' ? 
                    <td className="p-4 w-1/6 mt-5">
				<button  className="bg-blue-900 my-1 mt-7 text-white px-5 py-2 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50"  onClick={e => handleapprove(e,blog)} >Approved</button>
				</td>
                :

                <td className="p-4 w-1/6 mt-5">
				<button  className="bg-blue-900 my-1 mt-7 text-white px-5 py-2 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50" onClick={e => handlepen(e,blog)}>Pending</button>
				</td>

                }
				
        <td className="p-2  w-1/6 mt-4">{blog.description}</td>
			  </tr>

          )
        })
      }
		</tbody>
	    </table>

  )

}
    </div>
  )
}
